/*************************************

    CUSTOM NAVBAR

    = page-header
    = navbar
       navbar-fixed  - page-header-fixed modifier
       menu-trigger
       brand
       logo
       social-list
       reservation
       dropdown-menu

    [states and dropdown-menu]
    [affix - transform menu]

*************************************/
.navbar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 78px;
  margin: 0;
  text-transform: uppercase;
  border: none;
  border-radius: 0;
  z-index: 1030;
  box-shadow: 0 0.19444rem 0.55556rem rgba(0, 0, 0, 0.15) !important;
}

.navbar a:focus {
  color: #ccc;
}

@media (min-width: 768px) {
  .navbar {
    height: auto;
  }
}

.navbar.affix {
  position: fixed;
}

@media (min-width: 1600px) {
  .navbar.affix {
    top: 0 !important;
  }
  .navbar.affix .navbar-menu {
    height: 64px;
  }
  .navbar.affix .navbar-menu > li > a {
    font-size: 14px;
  }
}

@media (max-width: 767px) {
  .navbar.affix {
    top: 0 !important;
    height: 60px;
  }
}

.navbar__topline {
  position: relative;
  display: flex;
  top: 0;
  height: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

@media (min-width: 1600px) {
  .navbar__topline {
    position: absolute;
    width: 100%;
    height: 100%;
    border-bottom-color: rgba(0, 0, 0, 0);
  }
  .navbar__topline .navbar__toggle-reservation {
    display: none !important;
  }
}

@media (min-width: 768px) {
  .navbar__topline .navbar__toggle-reservation {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.navbar-header.container {
  display: flex;
  justify-content: space-between;
}

.navbar-header.container:before, .navbar-header.container:after {
  display: none;
}

.navbar-header.container > * {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

@media (min-width: 1600px) {
  .navbar-header.container {
    width: 100%;
  }
}

@media (max-width: 1599px) {
  .navbar-header.container {
    padding: 15px;
    align-items: center;
  }
}

@media (max-width: 767px) {
  .navbar-header.container {
    width: 100%;
  }
}

.navbar-brand {
  display: block;
  height: auto;
  padding: 0;
  border-right: 1px solid transparent;
  z-index: 1000;
}

.navbar > .container .navbar-brand {
  margin-left: 0;
}

.navbar-brand .logo {
  height: 47px;
  width: 47px;
  display: inline-block;
  overflow: hidden;
}

@media (max-width: 767px) {
