    height: 36px;
    width: 36px;
  }
}

.navbar .social-list {
  padding-left: 25px;
  border-left: 1px solid transparent;
}

.navbar__menu > .container {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
}

.navbar__menu > .container:before, .navbar__menu > .container:after {
  display: none;
}

@media (max-width: 1599px) {
  .navbar__menu .navbar__toggle-reservation {
    display: none !important;
  }
}

.navbar__toggle-reservation {
  display: inline-flex;
  flex-shrink: 0;
  padding: 0;
  letter-spacing: 0.2em;
  color: #c6aa28;
  display: inline-block;
  font-size: 14px;
  text-decoration: none;
}

.navbar__toggle-reservation .fa {
  display: inline-block;
  font-size: 1.11em;
  padding-right: 10px;
  letter-spacing: normal;
}

/**        [states and dropdown-menu]         **/
.navbar-menu {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 115px;
  transition: 0.30s ease;
}

.navbar-menu a {
  letter-spacing: 0.2em;
}

.navbar-menu .dropdown-menu {
  float: none !important;
  min-width: 165px;
  display: block;
  left: 0;
  right: auto;
  top: 100%;
  padding: 0;
  opacity: 0;
  border: none;
  box-shadow: 0 0.19444rem 0.55556rem rgba(0, 0, 0, 0.15);
  visibility: hidden;
  transform: translateY(15px);
}

.navbar-menu .dropdown-menu a {
  display: flex;
  justify-content: space-between;
  padding: 10px 15px;
  font-size: 14px;
  border-bottom: 1px solid #dedede;
  font-weight: 700;
  line-height: 1;
}

.navbar-menu .dropdown-menu a *:nth-child(2) {
  margin-left: 0.41667rem;
}

.navbar-menu .dropdown-menu a:hover {
  color: #000 !important;
}

.navbar-menu .dropdown-menu li:last-child > a {
  border-bottom-color: transparent;
}

.navbar-menu > li {
  position: relative;
  display: flex;
  margin-right: 5.10204%;
}

.navbar-menu > li:last-child {
  margin-right: 0;
}

.navbar-menu > li > a {
  display: inline-flex;
  align-items: center;
  padding: 5px 0;
  color: #333333;
  box-shadow: inset 0 0 0 #c6aa28;
  transition: all 0.4s ease;
}

.navbar-menu > li > a .fa-caret-down {
  font-size: 11px;
  margin-left: 4px;
}

@media (max-width: 1599px) {
  .navbar-menu > li > a {
    padding: 15px 0;
  }
}

.navbar-menu > li > .dropdown-menu {
  transition: transform 0.4s 0s, opacity 0.4s ease 0s, visibility 0s 0.4s;
}

.navbar-menu > li > .dropdown-menu.pull-right {
  left: -100%;
}

.navbar-menu > li:hover > a {
  box-shadow: inset 0 -4px 0 #c6aa28;
}

.navbar-menu > li:hover > .dropdown-menu {
  z-index: 1040;
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
  transition: transform 0.4s 0s, opacity 0.4s ease 0s, visibility 0s 0s;
}

.navbar-menu > li li {
  position: relative;
}

.navbar-menu > li li .dropdown-menu {
  left: 100%;
  right: auto;
  top: 0;
  margin: 0;
  transform: translateX(10px);
  border-top-left-radius: 0;
}

.navbar-menu > li li .dropdown-menu > li:first-child > a:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: -4px;
  width: 4px;
  background-color: #c6aa28;
  transform-origin: 100% 0;
  transform: scaleX(0);
  transition: transform 0.12s 0.15s linear;
}

.navbar-menu > li li .dropdown-menu.pull-right {
  left: 0;
  transform: translateX(calc(-100% - 10px));
  border-top-left-radius: 2px;
  border-top-right-radius: 0;
}

.navbar-menu > li li .dropdown-menu.pull-right > li:first-child > a:before {
  left: calc(100%);
  transform-origin: 0 0;
}

.navbar-menu > li li:hover > .dropdown-menu {
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
  transition: transform 0.2s 0s;
  z-index: 1040;
}

.navbar-menu > li li:hover > .dropdown-menu.pull-right {
  transform: translateX(calc(-100%));
}

.navbar-menu > li li:hover > .dropdown-menu > li:first-child > a:before {
  transform: scaleX(1);
}

@media (max-width: 1599px) {
  .navbar-menu {
    height: auto;
    min-height: auto;
    justify-content: center;
  }
}

@media (min-width: 1600px) {
  .navbar-header {
    padding: 0 50px;
    width: auto;
    margin: 0;
  }
  .navbar-nav a {
    font-size: 18px;
  }
  .navbar-brand {
    padding-right: 50px;
    border-right: 1px solid #dedede;
  }
  .navbar .social-list {
    padding-left: 50px;
    border-left: 1px solid #dedede;
  }
}

