  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
}

.service-category__slide-link {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  padding: 30px;
  align-items: center;
  justify-content: center;
  z-index: 2;
  overflow: hidden;
}

.service-category__slide-link::after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.60;
  z-index: 1;
  transition: 300ms;
}

.service-category__slide-link h2 {
  position: relative;
  z-index: 2;
  font-size: 1.77778rem;
  line-height: 1.4;
  margin-bottom: 0;
