  .control-panel--mobile {
    position: absolute;
    top: auto;
    right: 15px;
    bottom: 30px;
    left: 15px;
  }
}

/**        = elemets for video         **/
.btn-play {
  position: relative;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: #333333;
  cursor: pointer;
  z-index: 5;
}

.btn-play:after {
  content: ' ';
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -1.22222em 0 0 -0.55556em;
  border-style: solid;
  height: 0;
  width: 0;
  border-color: transparent transparent transparent #c6aa28;
  border-width: 1.22222em 0 1.22222em 1.66667em;
  transition: all 0.4s ease;
}

.btn-play:hover, .btn-play:focus {
  background-color: #ccc;
}

.btn-play:active {
  background-color: #fafafa;
}

.btn-play:active:after {
  border-left-color: #888;
}

/**        = video         **/
.video-contain {
  position: relative;
  height: 0;
  padding-bottom: 56.25%;
  border-top-right-radius: 3px;
  border: 1px solid transparent;
  /**         helper classess         **/
}

.video-contain__preview {
  position: absolute;
  height: 100%;
  left: 0;
  top: 0;
  width: 100%;
  opacity: 1;
  visibility: visible;
  z-index: 2;
}

.video-contain .video-controls {
  left: 0;
  top: 0;
  width: 100%;
  opacity: 1;
  visibility: visible;
  z-index: 2;
}

.video-contain__preview {
  background-size: cover;
  background-repeat: no-repeat;
  transition: all 0.4s ease;
}

.video-contain .btn-play {
  /**         width 80px         **/
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -40px;
  margin-left: -40px;
  visibility: visible;
  opacity: 1;
}

.video-contain__wrapper, .video-contain__wrapper embed,
.video-contain__wrapper object,
.video-contain__wrapper iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  transition: opacity 500ms 500ms;
}

.actived .video-contain__wrapper, .actived .video-contain__wrapper embed, .video-contain__wrapper .actived embed,
.actived .video-contain__wrapper object, .video-contain__wrapper .actived object,
.actived .video-contain__wrapper iframe, .video-contain__wrapper .actived iframe {
  opacity: 1;
}

.video-contain__preview.hover {
  border-color: #f8f8f8;
}

.video-contain__preview.hover .overlay {
  background-color: rgba(255, 255, 255, 0.85);
}

.video-contain__preview.hover .flag {
  opacity: .7;
}

.video-contain.actived .video-contain__preview,
.video-contain.actived .btn-play {
  visibility: hidden;
  opacity: 0;
}

/**        = Lightbox         **/
.blueimp-gallery-controls .prev,
.blueimp-gallery-controls .next {
  width: 40px;
  height: 40px;
  transform: rotate(45deg);
  transition: all .4s;
  border-width: 1px;
  border-color: transparent transparent #fff #fff;
  background-color: transparent;
  border-radius: 0;
}

.blueimp-gallery-controls .prev {
  left: 40px;
}

.blueimp-gallery-controls .next {
  right: 40px;
  border-color: #fff #fff transparent transparent;
}

.blueimp-gallery-controls .close {
  top: 40px;
  right: 40px;
  font-size: 34px;
  width: 1em;
  height: 1em;
  transform: rotate(45deg);
}

.blueimp-gallery-controls .close:before, .blueimp-gallery-controls .close:after {
  left: 0;
  top: 0;
  position: absolute;
  content: '';
  background-color: #fff;
}

.blueimp-gallery-controls .close:before {
  top: 50%;
  width: 1em;
  height: 2px;
  margin-top: -1px;
}

.blueimp-gallery-controls .close:after {
  left: 50%;
  width: 2px;
  height: 1em;
  margin-left: -1px;
}

.blueimp-gallery-controls > .indicator > li {
  position: relative;
  background-size: cover;
  transition: all .4s;
  z-index: 1;
  border: none;
}

.blueimp-gallery-controls > .indicator > li:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.7);
  transition: all .4s;
}

.blueimp-gallery-controls > .indicator > li.active:after {
  opacity: 1;
}

/*************************************

    SECTION PANELS

    = panels-wrapper
       slide
       --dark

    = panel-wd
       slide
       --right
       --dark

    = panel-board
       title
       main
       --right

    = hm-history
       carousel
       controls
       controls--mobile
       context

*************************************/
.panels-wrapper {
  position: relative;
  background-color: #fafafa;
  min-height: 640px;
  overflow: hidden;
}

.panels-wrapper--dark {
  background-color: #333333;
}

.panels-wrapper .container,
.panels-wrapper .row,
.panels-wrapper .row > *,
.panels-wrapper .panel-wd, .panels-wrapper__slide,
.panels-wrapper .panel-board {
  height: 100%;
}

.panel-wd {
  position: absolute;
  left: 0;
  padding: 0;
}

.panel-wd:after {
  content: '';
  position: absolute;
  top: 50%;
  right: -25px;
  margin-top: -25px;
  width: 50px;
  height: 50px;
  background-color: #fafafa;
  transform: rotate(45deg);
  z-index: 0;
}

.panel-wd__slide {
  position: relative;
  min-height: 640px;
  overflow: hidden;
  z-index: 1;
}

.panel-wd__slide img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.panel-wd--right {
  right: 0;
  left: auto;
}

.panel-wd--right:after {
  left: -25px;
  right: auto;
}

.panel-wd--dark:after {
  background-color: #333333;
}

@media (max-width: 767px) {
  .panel-wd {
    min-height: auto;
    position: relative;
  }
  .panel-wd:after {
    display: none;
    top: auto;
    left: 50%;
    bottom: -25px;
    margin-left: -25px;
  }
  .panel-wd--right:after {
    top: 0;
    bottom: auto;
  }
}

.panel-wd__slide {
  min-height: auto;
}

.panel-wd__slide img {
  position: static;
  transform: translate(0, 0);
}

.panel-board {
  position: relative;
  padding: 3.88889rem 6.30631% 2.22222rem 0;
  transition: .4s;
  z-index: 1;
}

.panel-board__title, .hm-gallery-2__header h1, .hm-gallery-2__header .h1, .room-gallery__title {
  display: inline-block;
  padding-bottom: 13px;
  margin-top: 0;
  border-bottom: 1px solid #c6aa28;
}

.panel-board__main {
  margin-bottom: 2.77778rem;
}

.panel-board--right {
  padding: 3.88889rem 0 2.22222rem 6.30631%;
}

@media (max-width: 767px) {
  .panel-board {
    width: 100%;
    text-align: center;
    padding: 4.44444rem 0;
  }
}

/**         hm-history-2         **/
.hm-history-carousel .panel-wd__slide img {
  width: auto;
}

.hm-history .panel-wd__slide {
  min-height: 640px;
}

@media (min-width: 768px) {
  .hm-history .panel-wd__slide img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .hm-history .control-panel {
    position: absolute;
    left: 7.5%;
    right: 7.5%;
    bottom: 7.5%;
    max-width: 850px;
    margin: 0 auto;
  }
}

@media (min-width: 1920px) {
  .hm-history .panel-wd__slide img {
    width: 100%;
  }
}

@media (max-width: 767px) {
  .hm-history-carousel .panel-wd__slide img {
    width: 100%;
    max-width: 100%;
    height: auto;
  }
  .hm-history .panel-wd {
    position: relative;
  }
  .hm-history .panels-wrapper,
  .hm-history .panel-wd__slide {
    min-height: 0;
  }
}

/*************************************

    Mobile menu Slideout.js

************************************/
.menu-trigger {
  order: 1;
  position: relative;
  float: right;
  width: 40px;
  height: 30px;
  cursor: pointer;
}

.menu-trigger:before {
  position: absolute;
  top: 13px;
  left: 0;
  width: 100%;
  height: 4px;
  background: #333333;
  box-shadow: 0 6px transparent, 0 -8px #333333, 0 18px transparent, 0 8px #333333;
  content: '';
  transition: all .31s ease;
}

.menu-trigger:hover:before {
  box-shadow: 0 6px transparent, 0 -12px #333333, 0 18px transparent, 0 12px #333333;
}

.slideout-menu {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 0;
  width: 256px;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  display: none;
}

.slideout-panel {
  position: relative;
  z-index: 1;
}

.slideout-open,
.slideout-open body,
.slideout-open .slideout-panel {
  overflow: hidden;
}

.slideout-open .slideout-menu {
  display: block;
}

.navbar,
.page-footer {
  transition: all .3s ease;
}

.navbar.open,
.page-footer.open {
  transform: translate3d(256px, 0, 0);
}

.slideout-open .slideout-menu {
  display: block;
}

.over-page {
  visibility: hidden;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  opacity: 0;
  background: rgba(0, 0, 0, 0.6);
  transform: translate3d(0, 0, 0);
  transition: all .3s ease;
}

.over-page.open {
  opacity: .3;
  visibility: visible;
  background: #000;
  z-index: 1040;
  transform: translate3d(256px, 0, 0);
  transition: opacity .3s ease .3s;
}

.panel-menu {
  background-color: #fff;
}

.panel-menu .panel-menu-header {
  position: relative;
  width: 256px;
  text-align: center;
  cursor: pointer;
}

.panel-menu .panel-menu-header h1 {
  position: relative;
  display: inline-block;
  padding: 21px 0;
  margin: 0;
  line-height: 1;
  font-size: 36px;
}

.panel-menu .panel-menu-header h1::before {
  position: absolute;
  bottom: 12px;
  left: 0;
  width: 100%;
  content: '';
  opacity: 0;
  border-bottom: 2px solid;
  transform: scaleX(0);
  transform-origin: 100% 0%;
  transition: transform 300ms ease 300ms, opacity 300ms;
}

.panel-menu .panel-menu-header h1:hover::before {
  transform-origin: 0% 0%;
  transform: scaleX(1);
  opacity: 1;
  transition: transform 300ms ease 0s, opacity 300ms;
}

.panel-menu-content {
  position: absolute;
  top: 78px;
  left: 0;
  right: 0;
  bottom: 75px;
}

.panel-menu-content > .panel-list {
  -webkit-overflow-scrolling: touch;
  overflow: scroll;
  overflow-x: hidden;
  overflow-y: auto;
}

.panel-menu-heading {
  display: block;
  margin: 0 35px;
  height: 20px;
  font-size: 14px;
  line-height: 1.4;
  letter-spacing: 0.10em;
  text-transform: uppercase;
  overflow: hidden;
  text-align: center;
}

.panel-menu-heading:hover, .panel-menu-heading:focus, .panel-menu-heading:active {
  color: #000000;
}

.panel-menu-heading .icon-back {
  width: 20px;
  height: 24px;
  position: absolute;
  top: 1px;
  left: 4px;
  transform: translateX(-100px);
  opacity: 0;
  font-size: 45px;
  line-height: 20px;
  color: #c6aa28;
  transition: transform .3s ease-in-out, opacity .3s ease .15s;
}

.panel-menu-heading.active .icon-back {
  opacity: 1;
  transform: translateX(0);
}

.panel-menu .panel-list {
  position: absolute;
  top: 50px;
  left: 0;
  right: 0;
  bottom: 0;
}

.panel-menu .panel-list__next {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  transform: translate3d(-100%, 0, 0);
  background-color: #fff;
  z-index: 1;
  transition: transform 400ms ease 0s;
}

.panel-menu .panel-list a {
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  font-size: 13px;
  padding: 10px 20px;
  border-bottom: 1px solid #e0e0e0;
}

.panel-menu .panel-list li > a {
  transition: transform 400ms ease 0s;
  transform: translate3d(0, 0, 0);
}

.panel-menu .panel-list li.next > a {
  transform: translate3d(100%, 0, 0);
}

.panel-menu .panel-list li.next.active > .panel-list__next {
  transform: translate3d(0, 0, 0);
}

.panel-menu-footer {
  position: absolute;
  top: auto;
  left: 0;
  right: 0;
  bottom: 0;
}

.panel-menu .social-list {
  justify-content: center;
  text-align: center;
  padding: 10px 0;
}

.panel-menu .social-list > li > a {
  font-size: 18px;
}
